<template>
  <router-view>
    <notifications></notifications>
  </router-view>
</template>

<script>
  import isNull from 'lodash/isNull'

  export default {
    name: 'app',

    async created () {
      const userInfo = JSON.parse(sessionStorage.getItem('userInfo'));
      if (!isNull(userInfo)) {
        this.$store.commit('SET_USER_INFO', userInfo);
        await this.$store.dispatch('getUserInfo');
      } else {
        await this.$store.dispatch('getUserInfo');
      }
      const currentLesson = JSON.parse(sessionStorage.getItem('currentLesson'));
      if (!isNull(currentLesson)) {
        this.$store.commit('SET_CURRENT_LESSON', currentLesson);
      }
    },
    computed: {
      userInfo: {
        get () {
          return this.$store.state.userInfo
        }
      }
    }
  }
</script>
